<template>
	<view class="list">
		<sun-header :title="$t('log.transactions')"></sun-header>

		<!--  -->
		<view class="card">
			<!-- <view class="title">Today 12 JUN 2022 </view> -->
			<view class="item" v-for="(item, index) in list" :key="index" @click="goToDetail(item)">
				<view class="l">
					<view class="s">
						<image v-if="item.paymentStatus == 'success' && !item.refundStatus" src="/static/images/status/success.svg"
							mode="widthFix"></image>
						<image v-if="item.paymentStatus == 'success' && item.refundStatus" src="/static/images/status/back.svg"
							mode="widthFix"></image>
						<image v-if="item.paymentStatus.toLowerCase() == 'fail' || item.paymentStatus.toLowerCase() == 'cancel'" src="/static/images/status/fail.svg" mode="widthFix"></image>
						<image v-if="item.paymentStatus == 'pending' || item.paymentStatus == 'PENDING'" src="/static/images/status/wait.svg" mode="widthFix"></image>

					</view>
					<view class="n" v-if="!item.refundStatus">
						{{ $t(item.paymentStatus) }}
					</view>
					<view class="n" v-if="item.refundStatus">
						{{ $t('refunded') }}
					</view>
				</view>
				<view class="r">
					<view class="" style="flex: 1">
						<view class="m"> ${{ item.amount / 100 }} </view>
						<view class="t"> {{ item.acquirerTypeAbbreviation }}</view>
					</view>
					<view class="i">
						<image src="/static/images/status/icon.svg" mode="widthFix"></image>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import sunHeader from '@/components/sun-header/sun-header.vue'
	import api from '@/utils/api/index.js'
	import utils from '@/utils/util.js'
	export default {
		name: 'list',
		components: {
			sunHeader,
		},
		data() {
			return {
				creationTimeStart: '',
				creationTimeEnd: '',
				list: [],
				page: 1,
				pageSize: 20,
				isEnd: false
			}
		},
		computed: {},
		onLoad(e) {
			console.log(e,'ee')
			this.creationTimeStart = e.start || utils.getDate() + ' 00:00:00'
			this.creationTimeEnd = e.end || utils.getDate() + ' 23:59:59'
		},
		mounted() {
			// this.getList()
		},
		onShow() {
			this.getList(true)
		},
		onReachBottom() {
			if (!this.isEnd) {
				this.page = this.page + 1
				this.getList()
			}
		},
		methods: {
			async getList(type) {
				if(type){
					this.page = 1
					this.isEnd = false
					this.list = []
				}
				utils.showLoading()
				let o = {
					creationTimeStart: this.creationTimeStart,
					creationTimeEnd: this.creationTimeEnd,
					index: this.page,
					pageSize: this.pageSize,
				}

				let res = await api.pageTransactionAndTransactionMetadata(o)
				let {
					data,
					code
				} = res
				if (code === '000000') {
					let list = data.records
					if (list.length < this.pageSize) {
						this.isEnd = true
					}
					this.list = this.list.concat(list)
					if (this.list.length == 0) {
						utils.showText('暂无数据')
					}
				} else {
					utils.showText(res.message)
				}
			},

			goToDetail(item) {
				let obj = {
					paymentStatus:item.paymentStatus,
					refundStatus:item.refundStatus,
					acquirerTypeAbbreviation:item.acquirerTypeAbbreviation,
					acquirerTransactionId:item.acquirerTransactionId,
					acquirerType:item.acquirerType,
					storeName:item.storeName,
					amount:item.amount,
					id:item.id,
					lastUpdateTime:item.lastUpdateTime
				}
				uni.navigateTo({
					url: '/pages/paymentHistory/detail/detail?item=' + encodeURIComponent(JSON.stringify(obj))
				})
			},
		},
	}
</script>

<style lang="scss" scoped>
	@import 'list.scss';
</style>